<template>
<!-- Start chats tab-pane -->
<div class="tab-pane fade show active" id="pills-chat" role="tabpanel" aria-labelledby="pills-chat-tab">
    <!-- Start chats content -->
    <div>
        <div class="px-3 pt-3">
            <h4 class="mb-4 d-flex justify-content-between align-items-center">Settings </h4>
        </div>

        <div class="px-3 pt-0">
            <div id="settingprofile" class="accordion">
                <AccordionItem id="allcall" :title="$t('Call')+'/'+$t('SMS')" :expanded="false">

                    <div class="accordion-body p-2">

                        <ul class="nav nav-tabs accordiontabs" id="myTab" role="tablist">
                            <listItem text="Call Setup" @click="activeTab('callSetup')" :className="{'active':activeLeftLink=='callSetup'}"/>
                            <listItem text="Twilio List" @click="activeTab('twilioList')" :className="{'active':activeLeftLink=='twilioList'}"/>
                        </ul>

                    </div>
                </AccordionItem>
               
                <AccordionItem id="allcall" title="Video" :expanded="false">

                    <div class="accordion-body p-2">

                        <ul class="nav nav-tabs accordiontabs" id="myTab" role="tablist">
                            <listItem text="Video" @click="activeTab('video')" :className="{'active':activeLeftLink=='video'}"/>
                        </ul>

                    </div>
                </AccordionItem>
                <AccordionItem id="allcall" title="Chat" :expanded="false">

                    <div class="accordion-body p-2">

                        <ul class="nav nav-tabs accordiontabs" id="myTab" role="tablist">
                            <listItem :text="$t('Chat')" @click="activeTab('Chat')" :className="{'active':activeLeftLink=='Chat'}"/>
                        </ul>

                    </div>
                </AccordionItem>
                <AccordionItem id="allcall" title="Notification" :expanded="false">

                    <div class="accordion-body p-2">

                        <ul class="nav nav-tabs accordiontabs" id="myTab" role="tablist">
                            <listItem text="Notification" @click="activeTab('notification')" :className="{'active':activeLeftLink=='notification'}"/>
                        </ul>

                    </div>
                </AccordionItem>

            </div>
        </div>

    </div>
    <!-- Start chats content -->
</div>
<!-- End chats tab-pane -->
</template>

<script>
import AccordionItem from '@/components/Call//AccordionItem.vue';
import listItem from '@/components/Common/ListItem.vue';
export default {
    inject: ['responsiveCssFunction'],
    components: {
        AccordionItem,
        listItem
    },
    props: {
        store: {
            type: Object
        }
    },
    data() {
        return {
            activeLeftLink:''
        };
    },
    methods: {
        activeTab(event){
           this.activeLeftLink = event
           this.$emit('settingLeftTabActive',event)
           this.responsiveCssFunction()
        }
    }
}
</script>
