<template>
<div class="dialpad-section">
    <div class="px-3 pt-3">
        <h4 class="mb-4 backarrow handpointer" @click="$emit('viewType','call')"><i class="fa fa-angle-left"></i> Back </h4>
    </div>
    <div class="px-3 pt-3">
        <div class="dialnumber-dropdwon text-center mb-4">
            <div class="dropdown">
                <div class="btn dropdown-toggle fw-bold show" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="true" @click="isExpanded =!isExpanded">Call from: (408) 834-7569 <i class="fa fa-angle-down"></i></div>
                <ul class="dropdown-menu " :class="{'show':isExpanded}" aria-labelledby="dropdownMenuButton1" style="position: absolute; inset: 0px auto auto 0px; margin: 0px; transform: translate(115px, 33px); right: 0%;" data-popper-placement="bottom-start">
                    <li><a class="dropdown-item fw-bold" href="#">+1 (261) 752-2978</a></li>
                    <li><a class="dropdown-item fw-bold" href="#">+1 (463) 571-2329</a></li>

                </ul>
            </div>

        </div>
        <div class="dialinput">
            <input type="tel" class="form-control" placeholder="Enter a number" v-model="dialInput" @input="searchContact" />
        </div>
        <div class="dialpadbx">
            <div class="dialpadbx-rw">
                <DialPadDigit :digit="1" @input="dialpaidValue" />
                <DialPadDigit :digit="2" @input="dialpaidValue" />
                <DialPadDigit :digit="3" @input="dialpaidValue" />
            </div>
            <div class="dialpadbx-rw">
                <DialPadDigit :digit="4" @input="dialpaidValue" />
                <DialPadDigit :digit="5" @input="dialpaidValue" />
                <DialPadDigit :digit="6" @input="dialpaidValue" />
            </div>
            <div class="dialpadbx-rw">
                <DialPadDigit :digit="7" @input="dialpaidValue" />
                <DialPadDigit :digit="8" @input="dialpaidValue" />
                <DialPadDigit :digit="9" @input="dialpaidValue" />
            </div>
            <div class="dialpadbx-rw">
                <DialPadDigit digit="*" @input="dialpaidValue" />
                <DialPadDigit digit="0" @input="dialpaidValue" />
                <DialPadDigit digit="#" @input="dialpaidValue" />
            </div>
            <div class="botrow">
                <div id="call" @click="makeCall"><i class="fas fa-phone-alt"></i></div>
            </div>
        </div>
    </div>
</div>
</template>

  
<script>
import DialPadDigit from './DialPadDigit.vue';

export default {
    components: {
        DialPadDigit
    },
    props: {
        store: {
            type: Object
        }
    },
    data() {
        return {
            dialInput: '',
            contactsearch: [],
            isExpanded:false
        };
    },
    methods: {
        dialpaidValue(event) {
            this.dialInput += event
            this.searchContact()

        },
        makeCall() {
            if (this.store.$state.callToken) {
                var params = {
                    To: this.dialInput,
                };
                this.store.makeOutgoingCall(params)
            } else {
                this.ShowAlert(this.$t("ConnectDevice"), "failure", true, this.$t("Alert"));
            }

        },
        searchContact() {
            const filter = this.store.getContactList.filter(item => item.USER_PHONE ? item.USER_PHONE.includes(this.dialInput) : "")
            console.log("checkkk", filter)
        }
    }
}
</script>

  
