<template>
<div class="tab-pane fade show active" id="pills-chat" role="tabpanel" aria-labelledby="pills-chat-tab">
    <!-- Start chats content -->
    <div>
        <div class="px-3 pt-3">
            <h4 class="mb-4 d-flex justify-content-between align-items-center">Contacts <span><span class="gridview handpointer" @click="leftContactView('gridview')" v-if="contactView=='listview'"><i class="fas fa-th-large"></i></span> <span v-if="contactView=='gridview'" @click="leftContactView('listview')" class="listview handpointer"><i class="fas fa-list"></i></span></span></h4>
        </div>
        <div class="grid-view" v-if="contactView=='listview'">
            <div class="px-3 ">
                <div class="search-box chat-search-box">
                    <div class="input-group mb-3 rounded-3">
                        <input type="text" class="form-control bg-light" v-model="txtSearchChatGroups" id="UserSearch" @input="searchContact" placeholder="Search By Contact" aria-label="Search messages or users" aria-describedby="basic-addon1">
                        <span class="input-group-text text-muted bg-light pe-3 ps-3 border-0" id="basic-addon1">
                            <i v-if="txtSearchChatGroups" class="ri-close-line search-icon font-size-18 handpointer" @click="txtSearchChatGroups='';searchContact()"></i>
                            <i v-else class="ri-search-line search-icon font-size-18 handpointer"></i>
                        </span>
                    </div>
                </div> <!-- Search Box-->
            </div>
            <!-- .p-4 -->

            <!-- Start user status -->
            <div class="px-3 pb-4" dir="ltr">

                <ul class="nav nav-tabs customtabs" id="myTab" role="tablist">
                    <li class="nav-item " role="presentation" @click="changeTabs('company')">
                        <button class="nav-link" :class="{'active':activeTab=='company'}" type="button">All Contacts</button>
                    </li>
                    <li class="nav-item" role="presentation" @click="changeTabs('Department');departmentORLocationFilter()">
                        <button class="nav-link " :class="{'active':activeTab=='Department'}" type="button">Department</button>
                    </li>
                    <li class="nav-item" role="presentation" @click="changeTabs('location');departmentORLocationFilter()">
                        <button class="nav-link" :class="{'active':activeTab=='location'}" type="button">Location</button>
                    </li>
                    <li class="nav-item" role="presentation" @click="changeTabs('Favorite')">
                        <button class="nav-link" :class="{'active':activeTab=='Favorite'}" type="button">{{ $t('Favourite') }}</button>
                    </li>

                </ul>
                <!-- end user status carousel -->
            </div>
            <!-- end user status -->

            <!-- Start chat-message-list -->

            <div class="tab-content" id="myTabContent">

                <div class="tab-pane fade show " :class="{'active':activeTab=='company' || activeTab=='Favorite'}">
                    <div class="simplebar-content" v-if="filterdContacts.length==0  "><span class="no_record_found">{{ $t('NoDataTodisplay') }}</span></div>
                    <div class="chat-message-list px-2" data-simplebar>
                        <ul class="list-unstyled chat-list chat-user-list">
                            <li v-for="contact,index in filterdContacts" :key="index">
                                <a>
                                    <div class="d-flex" :class="{'align-items-center':contact.DEPARTMENT_NAME==null}">
                                        <div class="chat-user-img online align-self-center me-3 ms-0" v-if="contact.USER_IMAGE">
                                            <img :src="contact.USER_IMAGE ? contact.USER_IMAGE : 'https://talygenv.github.io/Vue-UI-Content/images/UC/uc_user_new.jpg'" class="rounded-circle avatar-xs" alt="">

                                        </div>
                                        <div class="avatar-xs me-2" v-else>
                                            <span class="avatar-title rounded-circle bg-red-subtle " :style="{ backgroundColor: getFirstTwoLettersWithColor(contact.CONTACTNAME).color }">
                                                {{ getFirstTwoLettersWithColor(contact.CONTACTNAME).letters}}
                                            </span>
                                        </div>
                                        <div class="flex-grow-1 overflow-hidden">
                                            <h5 class="text-truncate font-size-14 mb-1 handpointer" @click="contactDetail(contact)">{{ contact.CONTACTNAME }}</h5>
                                            <p class="chat-user-message text-truncate mb-0">{{ contact.DEPARTMENT_NAME  }}</p>
                                        </div>

                                    </div>
                                </a>
                            </li>
                        </ul>
                    </div>

                </div>
                <div class="tab-pane fade active show" :class="{'':activeTab=='Department'}" v-if="activeTab=='Department' || activeTab=='location'">

                    <div class="chat-message-list px-3" data-simplebar>

                        <div class="heading_department_group">
                            <template v-for="item,index of departmentAndLoactionContact">
                                {{ index }}<span>{{ departmentAndLoactionContact[index].length }}</span>
                                <div class="clear"></div>
                                <ul class="list-unstyled chat-list chat-user-list">

                                    <li v-for="contact,index in item">
                                        <a>
                                            <div class="d-flex">
                                                <div class="chat-user-img align-self-center me-2 ms-0">
                                                    <div class="chat-user-img online align-self-center me-3 ms-0" v-if="contact.USER_IMAGE">
                                                        <img :src="contact.USER_IMAGE" class="rounded-circle avatar-xs" alt="">

                                                    </div>
                                                    <div class="avatar-xs me-2" v-else>
                                                        <span class="avatar-title rounded-circle bg-red-subtle " :style="{ backgroundColor: getFirstTwoLettersWithColor(contact.CONTACTNAME).color }">
                                                            {{ getFirstTwoLettersWithColor(contact.CONTACTNAME).letters}}
                                                        </span>
                                                    </div>
                                                </div>
                                                <div class="flex-grow-1 overflow-hidden">
                                                    <h5 class="text-truncate font-size-14 mb-1 handpointer" @click="contactDetail(contact)">{{ contact.CONTACTNAME }}</h5>
                                                    <p class="chat-user-message text-truncate mb-0">{{ contact.DEPARTMENT_NAME ? contact.DEPARTMENT_NAME : '-' }}</p>
                                                </div>

                                            </div>
                                        </a>
                                    </li>

                                </ul>
                            </template>
                        </div>

                    </div>
                </div>

                <!-- <div class="tab-pane fade show" :class="{'active':activeTab=='location'}" id="pinned" role="tabpanel" aria-labelledby="pinned-tab">
                    <div class="chat-message-list px-3" data-simplebar style="display: block;" v-if="store.getLeftSideFilter.length > 0">
                        <div class="heading_department_group" v-for="item,index in store.getLeftSideFilter[1].data" :key="index">{{ item.user_city }}<span>{{ item.count}}</span>
                            <div class="clear"></div>
                            <ul class="list-unstyled chat-list chat-user-list">

                                <li v-for="contact,index in cityContacts(item.user_city)" :key="index">
                                    <a>
                                        <div class="d-flex">
                                            <div class="chat-user-img align-self-center me-2 ms-0">
                                                <div class="chat-user-img online align-self-center me-3 ms-0" v-if="contact.USER_IMAGE">
                                                    <img :src="contact.USER_IMAGE" class="rounded-circle avatar-xs" alt="">

                                                </div>
                                                <div class="avatar-xs me-2" v-else>
                                                    <span class="avatar-title rounded-circle bg-red-subtle " :style="{ backgroundColor: getFirstTwoLettersWithColor(contact.CONTACTNAME).color }">
                                                        {{ getFirstTwoLettersWithColor(contact.CONTACTNAME).letters}}
                                                    </span>
                                                </div>
                                            </div>
                                            <div class="flex-grow-1 overflow-hidden">
                                                <h5 class="text-truncate font-size-14 mb-1 handpointer" @click="contactDetail(contact)">{{ contact.CONTACTNAME }}</h5>
                                                <p class="chat-user-message text-truncate mb-0">{{ contact.DEPARTMENT_NAME ? contact.DEPARTMENT_NAME : '-' }}</p>
                                            </div>

                                        </div>
                                    </a>
                                </li>

                            </ul>
                        </div>

                    </div>
                </div> -->
                <div class="tab-pane fade show" :class="{'active':activeTab=='Favorite'}" id="archived" role="tabpanel" aria-labelledby="archived-tab">

                    <div class="chat-message-list px-3" data-simplebar>
                        <ul class="list-unstyled chat-list chat-user-list">

                            <li>
                                <a>
                                    <div class="d-flex align-items-center">
                                        <div class="chat-user-img offline align-self-center me-3 ms-0">
                                            <img src="assets/images/users/avatar-6.jpg" class="rounded-circle avatar-xs" alt="">

                                        </div>
                                        <div class="flex-grow-1 overflow-hidden">
                                            <h5 class="text-truncate font-size-14 mb-1">Derrick Webster</h5>
                                        </div>

                                    </div>
                                </a>
                            </li>

                        </ul>
                    </div>

                </div>

            </div>

            <!-- End chat-message-list -->
        </div>

        <div class="list-view" v-else>
            <div class="px-3 pt-0">
                <div class="accordion">
                    <div style="height: 100%;" class="accordion-item card  mb-2" v-for="itemType,index in store.getLeftSideFilter" :key="index">
                        <div class="accordion-header" id="departmentinfo">
                            <button class="accordion-button " :class="{'collapsed':activeDepLoc!=itemType.type}" @click="toggleDepLoc(itemType.type)" type="button" data-bs-toggle="collapse" data-bs-target="#department" aria-expanded="true" aria-controls="department">
                                <h5 class="font-size-14 m-0">{{ itemType.type }}</h5>
                            </button>
                        </div>
                        <div style="height: 100%;" id="department" class="accordion-collapse collapse show" aria-labelledby="departmentinfo" data-bs-parent="#settingprofile" v-if="activeDepLoc==itemType.type">
                            <div class="accordion-body p-2 chat-message-list" data-simplebar>
                                <div class="d-flex justify-content-between mb-2" v-for="item in itemType.data" :key="item.department_id">
                                    <p class="font-size-14 text-muted mb-2 handpointer" @click="deparmentAndLocation(itemType.type, itemType.type == 'Department' ? item.department_id :item.user_city)">{{ item.department_name }}</p>
                                    <h5 class="font-size-14">{{ item.count }}</h5>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- end personal info card -->

                    <!-- end security card -->

                </div>
            </div>
        </div>

    </div>
    <!-- Start chats content -->
</div>
</template>

<script>
import {
    getFirstTwoLettersWithColor
} from "@/assets/common/vue-common"
export default {
    name: "LeftContactTab",
    components: {

    },
    props: {
        store: {
            type: Object
        },
    },
    created() {
        this.fetchContacts();
        this.store.leftSideFilter();
    },
    methods: {
        async changeTabs(event) {
            this.activeTab = event;
            await this.fetchContacts();

        },
        toggleDepLoc(event) {
            this.activeDepLoc = this.activeDepLoc === event ? '' : event;

        },
        async leftContactView(event) {
            this.contactView = event;
            if (event === 'listview') {
                await this.fetchContacts();
            } else {
                this.$parent.changeActiveTab()
            }
            this.$parent.contactChatChange({
                view: event,
                userInformation: '',
                contactDetailShow: false
            });
        },
        contactDetail(event) {
            if (event.USER_PHONE) {
                this.store.contactInformation(`phone=${encodeURIComponent(event.USER_PHONE)}`)
            }
            this.$parent.contactChatChange({
                view: this.contactView,
                userInformation: event,
                contactDetailShow: true
            });
        },
        async deparmentAndLocation(type, id) {
            const query = type === 'Department' ?
                `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=&city=&departmentId=${id}` :
                `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=&city=${id}&departmentId=`;
            await this.store.getContact(query);
            this.$parent.contactChatChange({
                view: this.contactView,
                userInformation: '',
                contactDetailShow: false
            });
            this.$parent.changeActiveTab()
        },
        async fetchContacts() {
            this.txtSearchChatGroups = ''
            const query = `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=${this.activeTab}&city=&departmentId=`;
            await this.store.getContact(query);
            this.searchContact()
        },
        searchContact() {
            let searchQuery = this.txtSearchChatGroups.toLowerCase();
            const filterList = (list, key) => list.filter(item => item[key].toLowerCase().includes(searchQuery));
            this.filterdContacts = filterList(this.store.getContactList, 'CONTACTNAME');
            if (this.activeTab == 'Department' || this.activeTab == 'location') {
                this.departmentORLocationFilter()
            }
        },
        departmentContacts(name) {
            return this.filterdContacts.filter(item => item['DEPARTMENT_NAME'] == name)
        },
        departmentORLocationFilter() {
            this.departmentAndLoactionContact = []
            const groupedContacts = this.filterdContacts.reduce((acc, contact) => {
                if (this.activeTab == 'Department') {
                    const {
                        DEPARTMENT_NAME
                    } = contact;
                    if (!acc[DEPARTMENT_NAME]) {
                        acc[DEPARTMENT_NAME] = [];
                    }
                    acc[DEPARTMENT_NAME].push(contact);
                } else {
                    const {
                        CITY
                    } = contact;
                    if (!acc[CITY]) {
                        acc[CITY] = [];
                    }
                    acc[CITY].push(contact);
                }
                return acc;
            }, {});
            // Sort the keys alphabetically
            const sortedGroupedContacts = Object.keys(groupedContacts)
                .sort()
                .reduce((sortedAcc, key) => {
                    sortedAcc[key] = groupedContacts[key];
                    return sortedAcc;
                }, {});

            // Assign the sorted contacts to the data property
            this.departmentAndLoactionContact = sortedGroupedContacts;

        },
        cityContacts(name) {
            return this.filterdContacts.filter(item => item['CITY'] == name)
        },

    },

    setup() {
        return {
            getFirstTwoLettersWithColor
        }
    },
    data() {
        return {
            //grid and list view
            contactView: 'listview',
            //list view active tab
            activeTab: 'company',
            //grid view expend node
            activeDepLoc: '',
            txtSearchChatGroups: '',
            filterdContacts: [],
            departmentAndLoactionContact: []
        }
    },
}
</script>
