                    params.fileType = vm.filesdetails[0].fileType
                    params.size = vm.filesdetails[0].size
                    params.typePath = vm.filesdetails[0].typePath
                    params.url = vm.filesdetails[0].url
                    params.fileId = vm.filesdetails[0].fileId
                }
                vm.$socket.emit('createRoom', params);
                this.preview=null
                vm.$parent.closeGroup()
